@import '../../utils/styles/variables.scss';

.container {
  grid-area: header;
  display: flex;
  height: 80px;
}

.logo_block {
  display: flex;
  width: 240px;
  border-right: 1px solid $BORDER_COLOR;

  &>a {
    margin: auto;
  }
}

.buttons {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  align-items: center;
  padding-left: 36px;
}

.go_back {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 110px;
  height: 40px;
  background: none;
  border: 1px solid $BORDER_COLOR;

  &::before {
    content: url('../../utils/icons/arrow.svg');
    display: block;
    padding-right: 11px;
    width: 11px;
    height: 11px;
  }

}

.create_project_block {
  display: flex;
  align-items: center;
  height: 100%;
}

.new_project_button {
  @extend %dashboard-buttons-styles;
  width: 183px;

  &::after {
    content: url('../../utils/icons/plus.svg');
    margin-left: 11px;
  }
}

.bell {
  width: 40px;
  height: 40px;
  background: url('../../utils/icons/bell.svg');
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid $BORDER_COLOR;

  border-radius: 50%;
  margin-left: 12px;
  cursor: pointer;
}